body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.selectMenu {
  display: inline-block;
  width: 100px; }

h3 {
  display: inline-block;
  width: 100px; }

h2 {
  font-family: 'ZCOOL XiaoWei', serif;
  color: goldenrod;
  text-shadow: 3px 3px 6px #000000;
  font-size: 3em;
  margin-top: 10px; }

button {
  margin: 10px 2px 2px 0; }
  button:focus {
    outline: 0; }

.hidden-solution {
  margin-top: 75px; }

.new-game-button {
  position: absolute;
  top: 10%;
  left: 5%;
  width: 7em;
  height: 2.3em;
  font-size: 16px;
  border-radius: 7em;
  background: transparent;
  border: ridge goldenrod;
  box-shadow: 2px 2px 10px #000000;
  text-shadow: 2px 2px 4px #000000;
  color: goldenrod; }
  .new-game-button:hover {
    background: goldenrod;
    color: black;
    border: ridge #462000;
    cursor: pointer; }
  .new-game-button:active {
    -webkit-transform: translateY(2px);
            transform: translateY(2px); }

.submit-button {
  position: absolute;
  top: 10%;
  right: 5%;
  width: 5em;
  height: 2em;
  border-radius: 7em;
  background: transparent;
  font-size: 18px;
  border: ridge goldenrod;
  color: goldenrod;
  box-shadow: 2px 2px 10px #000000;
  text-shadow: 2px 2px 4px #000000; }
  .submit-button:hover {
    background: goldenrod;
    color: black;
    border: ridge #462000;
    cursor: pointer; }
  .submit-button:active {
    -webkit-transform: translateY(2px);
            transform: translateY(2px); }

html {
  background-image: url(/master-mind/static/media/wood-wallpapers.a1fd0394.jpg); }

.game-board-container {
  max-width: 95%;
  margin: auto; }

.guess-container {
  margin: 1%; }

.colorChoices {
  width: 75px;
  height: 75px;
  border-radius: 40px;
  box-shadow: 1px 1px 10px #000000;
  cursor: pointer; }

#red {
  background-image: radial-gradient(#f59b8f 1%, red 99%);
  border: 1px solid firebrick; }

#orange {
  background-image: radial-gradient(#f7c377 1%, orange 99%);
  border: 1px solid #ff8801; }

#yellow {
  background-image: radial-gradient(lightyellow 1%, yellow 99%);
  border: 1px solid #f7e228; }

#green {
  background-image: radial-gradient(lightgreen 1%, green 99%);
  border: 1px solid darkgreen; }

#blue {
  background-image: radial-gradient(#2584dd 1%, blue 99%);
  border: 1px solid #0707dd; }

#black {
  background-image: radial-gradient(#252424 5%, black 90%);
  border: 1px solid black; }

#ivory {
  background-image: radial-gradient(white 5%, ivory 95%);
  border: 1px solid #d6d6c9; }

#sienna {
  background-image: radial-gradient(#c98b68 1%, sienna 99%);
  border: 1px solid #813714; }

.colorPosition {
  display: inline-block;
  width: 45px;
  height: 45px;
  border-radius: 40px;
  border: 1px ridge goldenrod;
  margin: 14px 7px 0 7px;
  box-shadow: 2px 2px 10px #000000; }

.colorPicker {
  display: inline-block;
  width: 75px;
  height: 75px;
  border-radius: 40px;
  border: 1px solid goldenrod;
  box-shadow: 2px 2px 10px #000000;
  cursor: pointer; }
  .colorPicker:active {
    -webkit-transform: translateY(4px);
            transform: translateY(4px); }

label input {
  visibility: hidden;
  width: 0; }

.attempt-container {
  display: inline-block;
  max-width: 90%; }

.attempt-row {
  display: block; }

.score {
  display: inline-block;
  width: 35px;
  height: 35px;
  border-radius: 25px;
  border: 1px ridge goldenrod;
  margin: 22px 0 3px 0;
  box-shadow: 2px 2px 10px #000000; }

ul {
  list-style: none;
  padding: 10px; }

ol {
  padding: 10px;
  color: goldenrod;
  list-style-type: upper-roman;
  font-family: 'Times New Roman', Times, serif; }

.main-container {
  max-width: 95%;
  margin: auto; }

@media only screen and (max-width: 415px) {
  .colorPicker {
    width: 12vw;
    height: 12vw;
    margin: 1px auto; }
  .score {
    display: inline-block;
    width: 15px;
    height: 15px;
    border-radius: 15px;
    margin: 6px 0 6px 0; }
  .colorPosition {
    display: inline-block;
    width: 25px;
    height: 25px;
    border-radius: 25px;
    margin: 1px; } }

